.root{margin:60px 0}@media (min-width:768px){.root{margin:80px 0 120px}}.inner{margin:0 auto;max-width:100%;text-align:center;width:var(--page-width)}@media (min-width:768px){.slide{display:flex}}.slide__content,.slide__image{padding:0 var(--gutter-sm) 60px}@media (min-width:768px){.slide__content,.slide__image{flex:0 0 auto;width:50%}}.slide__content{position:relative}.content{display:block;left:0;opacity:0;padding:0 var(--gutter-sm);pointer-events:none;position:absolute;top:0;transition:opacity .3s ease-in-out;width:100%}.content.active{opacity:1;pointer-events:auto;transition-duration:.6s}.images{width:440px}.copy,.images{margin:0 auto;max-width:100%}.copy{background-color:var(--white-color);border-radius:var(--border-radius-box);padding:40px;text-align:left;width:480px}.indicators{bottom:-15px;left:0;position:absolute;width:100%}.learn-more{bottom:-70px;display:none;opacity:0;pointer-events:none;position:absolute;right:25%;transition:opacity .3s ease-in-out}@media (min-width:768px){.learn-more{display:block}}.learn-more a{color:var(--coffee-color);text-decoration:none}.learn-more:after{background-color:var(--coffee-color);content:"";display:block;height:1px;left:115%;position:absolute;top:50%;width:100vw}.learn-more.active{opacity:1;pointer-events:auto;transition-duration:.6s}


