.root{align-items:center;background-color:#fff;display:flex;height:var(--header-height);justify-content:space-between;left:0;padding:0 var(--gutter-sm);position:fixed;right:0;top:0}.logo,.logo-cta{flex:0 0 auto;width:200px}.logo-cta{display:none;position:relative;text-align:right;text-decoration:none}.logo-cta a.account-cta{align-items:center;display:flex;justify-content:flex-end;transition:opacity var(--transition-out)}.scrolled .logo-cta a.account-cta{opacity:0}.logo-cta span{color:var(--coffee-color);margin-right:20px}@media (min-width:768px){.logo-cta{display:block}}.mini-logo{align-items:center;background-color:var(--coffee-color);border-radius:20px;display:flex;height:40px;text-align:center;width:40px}.cta-button{background-color:var(--yellow-color);border-radius:8px;color:#fff;font-size:var(--font-size-p-small);padding:1em 2.75em;position:absolute;right:0;text-decoration:none;top:-120px;transform:translateY(-50%);transition:var(--transition-out)}.scrolled .cta-button{top:50%}.cta-button:hover{background-color:var(--dark-yellow-color)}.links{display:none;flex:1 1 100%;text-align:center}@media (min-width:768px){.links{display:block}}.links a{color:var(--font-color);display:inline-block;padding:1em;text-decoration:none;transition:color var(--transition-out)}.links a.active{color:var(--coffee-color)}.links a:hover{color:var(--coffee-color);transition-duration:var(--transition-in)}.toggle-icon{cursor:pointer;height:16px;position:relative;transform:translateY(-2px);width:24px}@media (min-width:768px){.toggle-icon{display:none}}.toggle-icon .toggle-icon_bar-one,.toggle-icon .toggle-icon_bar-three,.toggle-icon .toggle-icon_bar-two{background-color:#000;height:4px;left:0;position:absolute;top:0;transition:.12s ease-in-out;width:100%}.toggle-icon .toggle-icon_bar-two{top:50%}.toggle-icon .toggle-icon_bar-three{top:100%}.open .toggle-icon .toggle-icon_bar-one{top:50%;transform:rotate(-45deg)}.open .toggle-icon .toggle-icon_bar-two{opacity:0}.open .toggle-icon .toggle-icon_bar-three{top:50%;transform:rotate(45deg)}.overlay{background-color:var(--white-color);bottom:0;left:0;opacity:0;pointer-events:none;position:fixed;right:0;top:var(--header-height);transition:.5s ease-in-out}@media (max-width:767px){.open .overlay{opacity:1;pointer-events:auto;transition-duration:var(--transition-in)}}.overlay__links{padding:10px var(--gutter-sm) var(--gutter-sm);text-align:center}.overlay__links a{border-top:1px solid var(--coffee-color);color:var(--font-color);display:block;font-size:var(--font-size-p);padding:1em var(--gutter-sm);text-decoration:none}.overlay__links a.active{color:var(--coffee-color)}.overlay__links a:last-child{border-bottom:1px solid var(--coffee-color)}.footer__links{display:flex;flex-wrap:wrap;padding:0 var(--gutter-md) var(--gutter-md);text-align:center}.footer__links .list{flex:0 0 auto;margin:0;padding:0;width:50%}.footer__links .list:nth-child(3) li{margin:0}.footer__links .list li{font-family:var(--font-apercu);font-size:1rem;list-style:none;margin:1em 0}.footer__links .list li>a{color:inherit;text-decoration:none;transition:color var(--transition-out)}.footer__links .list li>a:hover{color:var(--coffee-color);transition-duration:var(--transition-in)}


