.root{margin:60px 0 45px;position:relative}@media (min-width:768px){.root{margin:120px 0}}.inner{margin:0 auto;max-width:100%;width:var(--page-width)}.header{margin-bottom:30px;padding:0 var(--gutter-sm)}@media (min-width:768px){.header{margin-bottom:60px;text-align:center}}@media (min-width:768px){.blocks{display:flex;flex-wrap:wrap;justify-content:flex-end}}.block-copy,.block-image{padding:var(--gutter-sm)}@media (min-width:768px){.block-copy,.block-image{flex:0 0 auto;width:50%}}@media (min-width:1024px){.block-copy{padding-bottom:150px}}p.callout{font-size:var(--font-size-h3)}@media (max-width:767px){p.callout{font-size:var(--font-size-h3-small);margin-top:0}}@media (min-width:768px){.copy{margin:30px auto;max-width:100%;width:400px}}@media (min-width:768px){.root:not(.inverted) .block-copy{order:-1}}.block-image{text-align:center}@media (min-width:768px){.block-image{margin:30px auto}}.images{width:440px}.images,.lead-in{margin:0 auto;max-width:100%}.lead-in{padding:0 var(--gutter-sm);text-align:center;width:400px}@media (min-width:768px){.lead-in{padding:0;width:50%}}@media (min-width:1024px){.lead-in{margin:0 0 0 auto;text-align:left}}.line-wrapper{bottom:160px;display:none;left:50%;position:absolute;right:var(--gutter-md);transform:translateX(-50%);width:160px}@media (min-width:1024px){.line-wrapper{display:block}}


