.root{background:var(--white-color);padding:30px 0 60px}@media (min-width:768px){.root{padding:120px 0}}.inner{margin:0 auto;max-width:100%;width:var(--page-width)}@media (min-width:768px){.inner{display:flex}}.content,.form{padding:var(--gutter-sm)}@media (min-width:768px){.content,.form{flex:0 0 auto;width:50%}}.lists{display:none}@media (min-width:768px){.lists{display:flex;flex-wrap:wrap;margin:2.25em 0 3em}}.list{flex:0 0 auto;margin:0;padding:0;width:33.33%}.list:nth-child(4) li{margin:0}.list li{font-family:var(--font-apercu);font-size:1rem;list-style:none;margin:.4em 0}.list li>a{color:inherit;text-decoration:none;transition:color var(--transition-out)}.list li>a:hover{color:var(--coffee-color);transition-duration:var(--transition-in)}.copy-wrapper{margin:20px auto 0;max-width:100%;width:400px}@media (min-width:768px){.copy-wrapper{margin:30px auto}}.copyright{font-size:1rem}


