.root{background-color:var(--yellow-color);border-radius:8px;color:#fff;display:inline-block;font-size:var(--font-size-p);margin-top:1em;max-width:100%;text-align:center;text-decoration:none;transition:background-color var(--transition-out)}.root,.root.inline{padding:.9em 2em}.root:not(.inline){max-width:340px;width:100%}@media (min-width:768px){.root:not(.inline){max-width:100%;padding:.9em 1em}}.root:hover{background-color:var(--dark-yellow-color);transition-duration:var(--transition-in)}.root.smaller{padding:.6em 2em}.root[disabled],.root[disabled]:hover{background-color:#999;cursor:not-allowed}


