.root{margin:45px 0 60px}@media (min-width:768px){.root{margin:120px 0 160px}}.inner{display:flex;flex-wrap:wrap;justify-content:center;margin:0 auto;max-width:100%;width:var(--page-width-wide)}.item{flex:0 0 auto;padding:var(--gutter-sm);width:50%}@media (min-width:480px){.item{width:33.33%}}@media (min-width:768px){.item{width:25%}}@media (min-width:1024px){.item{padding:var(--gutter-md);width:20%}}a.image-wrapper{cursor:pointer;display:block;height:100px;position:relative;width:100%}a.image-wrapper img{-o-object-fit:contain;height:100%;max-width:100%;object-fit:contain;opacity:.6;transition:var(--transition-out)}a.image-wrapper:hover img{opacity:1;transform:translateY(-20px)}a.image-wrapper:hover .caption{opacity:1}a.image-wrapper .caption{bottom:-10px;color:var(--coffee-color);height:30px;left:0;line-height:30px;opacity:0;position:absolute;text-align:center;transition:var(--transition-out);width:100%}


