.root{display:block;position:relative}.inner{margin:0 auto;max-width:100%;min-height:640px;width:100%}@media (min-width:768px){.inner{display:flex}}.inner h1.secondary{font-size:var(--font-size-h2)}@media (max-width:767px){.inner h1.secondary{font-size:var(--font-size-h2-small);margin-top:0}}.content,.media{padding:60px var(--gutter-sm)}@media (min-width:768px){.content,.media{flex:0 0 auto;padding:60px var(--gutter-md);width:50%}}.content{align-items:center;display:flex}.content,.media{background:var(--cream-color)}.media{position:relative}@media (max-width:767px){.media{padding-bottom:60%}}@media (min-width:768px){.copy{margin:30px auto;max-width:100%;width:400px}}.line-wrapper{bottom:-90px;display:none;position:absolute;right:var(--gutter-md)}@media (min-width:1024px){.line-wrapper{display:block}}.line-wrapper a{color:var(--coffee-color);opacity:.9;position:absolute;right:0;top:0;transform:rotate(-90deg) translateY(-70%) translateX(120%);transform-origin:top right;white-space:nowrap}


