.inner{margin:60px auto 90px;max-width:100%;padding:0 10px;text-align:center;width:var(--page-width)}@media (min-width:480px){.inner{padding:0 var(--gutter-sm)}}@media (min-width:768px){.inner{margin:120px auto}}.slides{padding-bottom:60px;position:relative}.indicators{bottom:0}.indicators,.slide{left:0;position:absolute;width:100%}.slide{opacity:0;top:0;transition:opacity .3s ease-in-out}.slide .quote{align-items:center;background-image:url(/static/images/bubble_1b.png);background-position:50%;background-repeat:no-repeat;background-size:contain;display:flex;flex-direction:column;justify-content:center;margin:0 auto 20px;max-width:100%;min-height:400px;padding:30px;width:680px}@media (min-width:480px){.slide .quote{background-image:url(/static/images/bubble_1a.png);margin:0 auto 45px;min-height:330px;padding:40px}}@media (min-width:768px){.slide .quote{padding:60px}}.slide.active{opacity:1;transition-duration:.6s}.slide blockquote{color:var(--coffee-color);font-family:var(--font-esteban);font-size:var(--font-size-h4);margin-left:auto;margin-right:auto;max-width:560px}@media (min-width:768px){.slide blockquote{font-size:var(--font-size-quote)}}.slide span{display:inline-block;font-size:var(--font-size-h4);margin-bottom:1rem}@media (min-width:768px){.slide span{font-size:2rem}}


